body {
  font-family: "Poppins", sans-serif;
}



.btn-add-cart{
  transition: 0.3s ease;
  bottom: -100px;
}
.box-prod:hover .btn-add-cart{
  bottom: 0;
}

.btn-fav{
  transition: 0.3s ease;
  right: -10px;
}
.btn-prod:hover .btn-fav {
  right: 10px;
}

.acrivesize {
  background-color: rgb(241 245 249);
}

.styleShadow {
  box-shadow: rgba(0, 0, 0, 1) 0px 22px 100px 20px;
}